<template>
    <video-player  class="video-player vjs-custom-skin"
                 ref="videoPlayer"
                 :options="playerOptions"
                 :playsinline="true"
                 id="video_player"
               >
            </video-player>
</template>

<script>
    export default {
        name: "video_player",
        props:{
            videoUrl: String,
            state: Boolean
        },
        data(){
            return{
              playerOptions: {
                // playbackRates: [0.7, 1.0, 1.5, 2.0], //
                autoplay: false, //true,
                muted: false, // 
                loop: false, // 
                preload: 'auto', // <video>auto,
                language: 'zh-CN',
                aspectRatio: '16:9', //  - "16:9""4:3"
                fluid: true, // trueVideo.js player
                sources: [{
                type: "video/mp4",
                src: this.videoUrl, //urlhttp://39.96.169.188/video_play/?filename=13.mp4
                }],
                // poster: "../assets/DNF.jpg", //
                // width: document.documentElement.clientWidth,
                notSupportedMessage: '', //Video.js
            controlBar: {
                 timeDivider: true,
                 durationDisplay: true,
                 remainingTimeDisplay: false,
                 fullscreenToggle: true  //
                }
            }
            }
        },
        watch: {
            // videoUrl
            videoUrl: function (val) {
                // alert(val);
                if (val !== '') {
                    this.$refs.videoPlayer.player.src(val)
                }
            },
                    //  state
        state: function (val) {
            if (val) {
                // alert(val);
        this.$refs.videoPlayer.player.pause()
        }
        },
        }

    }
</script>

