<template>
    <div>
        <h1 v-loading="loading">{{threshold}}</h1>
        <el-form :model="numberValidateForm" ref="numberValidateForm" label-width="100px" class="demo-ruleForm" style="margin-top: 10%">

  <el-form-item
    label=""
    prop="threshold_update"
    :rules="[
      { required: true, message: ''},
      { type: 'number', message: ''}
    ]"
  >
    <el-input type="age" v-model.number="numberValidateForm.threshold_update" autocomplete="off"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="submitForm('numberValidateForm')"></el-button>
    <el-button @click="resetForm('numberValidateForm')"></el-button>
  </el-form-item>
</el-form>
    </div>
</template>

<script>
    export default {
        name: "threshold",
        data() {
      return {
         threshold:"",
        numberValidateForm: {
          threshold_update: ''
        },
        number_input:'',
        loading:true
      }
    },
        mounted(){
        this.getThreshold()
    },
        methods: {
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      getThreshold(){
          var _this=this;
          this.$ajax.post('/get_threshold/').then(function (response) {//
                // console.log(response);
                if (response['data'] != "") {//null""
                    this.threshold = ""+response['data'];
                    this.loading=false;
                    return true;
                }
                else {
                    this.threshold = "";
                    this.loading=false;
                }
            }.bind(this))
                .catch(function (error) {
                    _this.open4(error);
                    return false;
                })
      },
      submitForm(formName){
          var _this=this;
                      this.$refs[formName].validate((valid) => {
                if (valid) {
                    console.log('successfully submit!!');
                    this.$ajax.post('/set_threshold/', this.$qs.stringify({//qsget uri
                        threshold_update: this.numberValidateForm.threshold_update,
                    }))
                        .then(function (response) {
                            if (response['data'] != "") {
                                  this.$notify({
                                    title: '',
                                    message: '',
                                    type: 'success'
                                    });
                                  this.getThreshold()
                            }
                            else {
                                this.open4("")
                            }
                        }.bind(this))
                        .catch(function (error) {
                            _this.open4(error)
                        })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
      },
      open4(text) {
        this.$message.error(text);
        },
    }
    }
</script>

<style scoped>

