<template>
    <div>
    <el-table
      :data="tableData"
      style="width: 100%"
      v-loading="loading">
      <el-table-column
        prop="date"
        label=""
        width="150">
      </el-table-column>
      <el-table-column
        prop="filename"
        label=""
        width="150">
      </el-table-column>
       <el-table-column
        prop="target"
        label=""
        width="200">
      </el-table-column>
      <el-table-column
        prop="status"
        label=""
        >
      </el-table-column>
       <el-table-column
        prop="result"
        label=""
        >
      </el-table-column>
      <el-table-column
         prop="status"
         label="" >
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="ImagePlay(scope.row.filename)" icon="el-icon-video-play" type="success" v-if="scope.row.status===''"></el-button>
      </template>
    </el-table-column>
    </el-table>
              <el-dialog
            title="(__)"
            :visible.sync="dialogVisible"
            ref="videoDialog"
            width="70%"
            height="70%"
            :close-on-click-modal="false"
            ><!--:before-close="handleClose"-->

          <ImagePlayer :Imageurl="ImageUrl" :state="state" ></ImagePlayer>
      </el-dialog>
   </div>
</template>

<script>
    import ImagePlayer from './ImagePlayer'
    export default {
        name: "abnormalImage",
        components:{
            ImagePlayer
        },
         data() {
            return {
                tableData: [],
                dialogVisible: false,
                ImageUrl:"",
                state:false,
                loading:true
            }
        },
        mounted(){
        this.getAbnormalImageList()
      },
        methods:{
            getAbnormalImageList(){
                this.$ajax.post('/getAbnormalImageList/').then(function (response) {//
                if (response['data'] != "") {//null""
                    var result=(response['data']);
                    this.tableData=result['data'];
                    this.loading=false;
                    return true;
                }
            }.bind(this))
                .catch(function (error) {
                    this.open4(error);
                    return false;
                })
            },
            ImagePlay(filename){//http://39.96.169.188 ???
          var Imageurl= "/image_play/?filename="+filename;
          // alert(Imageurl);
          this.dialogVisible=true;
          this.ImageUrl=Imageurl;
        },
            handleClose(done) {
            this.$confirm('')
          .then(_ => {
              // document.getElementById('video_iframe').remove();
            this.ImageUrl="";
            this.state=true;
            done();
          })
          .catch(_ => {});
      }
        }
    }
</script>

