<template>
    <div>
    <el-table
      :data="tableData"
      style="width: 100%"
      v-loading="loading">
      <el-table-column
        prop="order"
        label=""
        width="150">
      </el-table-column>
      <el-table-column
        prop="target"
        label="/"
        width="150">
      </el-table-column>
       <el-table-column
        prop="averageNum"
        label=""
        width="200">
      </el-table-column>
        </el-table>
        </div>
</template>

<script>
    export default {
        name: "rank",
        data(){
            return{
                tableData:[],
                loading:true
            }
        },
        mounted(){
            this.getRank()
        },
        methods:{
            getRank(){
                this.$ajax.post('/getRank/').then(function (response) {//
                if (response['data'] != "") {//null""
                    var result=(response['data']);
                    this.tableData=result['data'];
                    this.loading=false;
                    return true;
                }
            }.bind(this))
                .catch(function (error) {
                    this.open4(error);
                    return false;
                })
            }
        }
    }
</script>

