<!--templateelement-->
<template>
<div>
    <el-card class="box-card" style="margin-left: 25%;margin-top: 1%">
                <div slot="header" class="clearfix">
                <span style="font-size: x-large ;font-family: "></span>
                </div>
                <div class="text item">
                        &emsp;SSDFaster-RCNN
                    .mp4</div>
    <el-upload
    accept=".mp4"
    class="upload-demo"
    drag
    :auto-upload="false"
    ref="upload"
    action="/UploadVideo/"
    name="upload_video"
    :limit="1"
    :on-success="onSuccess"
    :on-error="onError"
    :on-remove="onRemove"
    :before-upload="beforeUpload"
    :on-change="onChange"
    >
  <i class="el-icon-upload"></i>
  <div class="el-upload__text"><em></em></div>
  <div class="el-upload__tip" slot="tip">(.mp4)</div>
    </el-upload>
    </el-card>
</div>


</template>

<script>
    export default {
        name: "discovery",
         data(){
           return{
               flag:-1,
           }
         },
        methods:{
        onSuccess(){
            this.$refs.upload.clearFiles();
            this.$notify({
                 title: '',
                 message: '....',
                 type: 'success'
        });
        },
        onError(){
             this.$notify({
                 title: '',
                 message: '',
                 type: 'error'
        });
        },
        beforeUpload(file){
            if(file.type=='video/mp4'){
                console.log("");
            }
            else {
                // this.$refs.upload.abort(file);
                // this.$refs.upload.clearFiles();
                this.$message({
                    type: 'error',
                    message: 'mp4'
                                });
                  return false
            }
        },
        onRemove(){
            this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
        },
        onChange(file){
            var _this=this;
            if(file.status=='ready'){
            this.$ajax.post('/beforeUploadVideo/',this.$qs.stringify({//qsget uri
                        filename: file.name
                    })).then(function (response) {//
                if (response['data'] != "") {//null""
                    // alert(response['data']);
                    _this.open4("");
                    _this.$refs.upload.clearFiles();
                    return false;//
                }
                else {
                    _this.$refs.upload.submit();
                    return true;//
                }
            }.bind(this))
                .catch(function (response) {
                    alert("catch");
                });

        }
        },
        open4(text) {
        this.$message.error(text);
        },
        sleep(n) {
        var start = new Date().getTime();
        while (1) {
            if (new Date().getTime() - start > n) {
                break;
            }
        }

         },
        }
    }
</script>

<style scoped>
.item {
    margin-bottom: 20px;
    /*font-family: "";*/
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 50%;

  }
</style>