  <div id="app">

   <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" router>
     <el-menu-item index="/" >
       <el-button type="text" style="font-family:  ;font-size: 30px;font-weight: 900" default-active>_(_</el-button>
     </el-menu-item>
