    <router-view></router-view>
    </transition>
<!--Login-->
   <el-dialog title="" :visible.sync="LoginFormVisible" width="350px" :close-on-click-modal="false"	>
     <el-form :model="form" :rules="rules" ref="form">
            <el-form-item label="" :label-width="formLabelWidth" prop="username">
                <el-input v-model="form.username" autocomplete="off" placeholder="//" size="small"></el-input>
            </el-form-item>
             <el-form-item label="" :label-width="formLabelWidth" prop="password">
                 <el-input v-model="form.password" autocomplete="off" placeholder="" show-password size="small"></el-input>
            </el-form-item>
     </el-form>
       <!--Dialog-->
       <el-dialog width="500px" title="" :visible.sync="RegisterDialogVisible" append-to-body :close-on-click-modal="false">
            <el-form :model="form1" :rules="rules1" ref="form1" size="small">
                <el-form-item label="" :label-width="formLabelWidth1" prop="username" >
                    <el-input v-model="form1.username" autocomplete="off" placeholder="(*^^*)" size="small" style="width: 70%;"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth1" prop="password" >
                     <el-input type="password" v-model="form1.password" autocomplete="off" size="small" style="width: 70%;" show-password></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth1" prop="checkpassword" >
                     <el-input type="password" v-model="form1.checkpassword" autocomplete="off" size="small"  style="width: 70%;"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth1" prop="gender">
                    <el-select v-model="form1.gender" placeholder="" size="small" style="width: 70%;">
                        <el-option label="" value="Male"></el-option>
                        <el-option label="" value="Female"></el-option>
                        <el-option label="" value="Other"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth1" prop="birthDate">
                    <el-date-picker type="date" placeholder="" v-model="form1.birthDate" size="small"  style="width: 70%;"
                    value-format="yyyy-MM-dd" ></el-date-picker>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth1" prop="eduDegree">
                    <el-select v-model="form1.eduDegree" placeholder="" size="small" style="width: 70%;">
                        <el-option label="" value="Bachelor"></el-option>
                        <el-option label="" value="Master"></el-option>
                        <el-option label="" value="Doctor"></el-option>
                        <el-option label="" value="Other"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth1" prop="email" >
                    <el-input v-model="form1.email" autocomplete="off" placeholder="" size="small" style="width: 70%;"></el-input>
                </el-form-item>
                <el-form-item label="1" prop="UserTags">
                    <br/>
                        <el-tag v-model="form1.UserTags"
                        :key="tag"
                        v-for="tag in form1.UserTags"
                        closable
                        :disable-transitions="false"
                        @close="handleClose(tag)">
                        {{tag}}
                        </el-tag>
                        <!--Tag widthclasscss-->
                        <el-input style="width: 90px"
                        class="input-new-tag"
                        v-if="inputVisible"
                        v-model="inputValue"
                        ref="saveTagInput"
                        size="small"
                        @keyup.enter.native="handleInputConfirm"
                        @blur="handleInputConfirm">
                        </el-input>
                        <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
                </el-form-item>
            </el-form>
             <div slot="footer" class="dialog-footer">
                 <el-button @click="RegisterDialogVisible = false"> </el-button>
                 <el-button type="primary" @click="onSubmit2('form1')"> </el-button>
            </div>
       </el-dialog>
  <div slot="footer" class="dialog-footer">
    <el-button type="text" @click="RegisterDialogVisible=true"></el-button>
    <el-button @click="LoginFormVisible = false"> </el-button>
    <el-button type="primary" @click="onSubmit('form')"> </el-button>
