<!---->
<script>
    import bg from './assets/a.jpg'
export default {
    name: 'app',
    //template
    components: {
        // home
    },
    props: {
        //
    },
    data() {
        var validatePass2 = (rule, value, callback) => {
            if (value !== this.form1.password) {
                callback(new Error('!'));
            }
            else{
                callback();
            }
        };
        return {
            url :bg,
            msg: 'Paper Sharing ',
             activeIndex: '/',
            input: '',
            form:{
                username:'',
                password:'',
            },
            rules:{
                username:[
                     { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 12, message: ' 5  15', trigger: 'blur' }
                ],
                password:[
                     { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 15, message: ' 5  15', trigger: 'blur' }
                ]
            },
            form1:{
                username:'',
                password:'',
                checkpassword:'',
                gender:'',
                birthDate:'',
                eduDegree:'',
                email:'',
                UserTags:[''],
            },
            rules1:{
                username:[
                     { required: true, message: '', trigger: 'blur' },
                     { min: 5, max: 12, message: ' 5  15', trigger: 'blur' }
                ],
                password:[
                     { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 15, message: ' 5  15', trigger: 'blur' }
                ],
                checkpassword:[
                     // { required: true, message: '', trigger: 'blur' },//valid
                     { validator: validatePass2, trigger: 'blur' },
                    { min: 5, max: 15, message: ' 5  15', trigger: 'blur' }
                ],
                gender: [
                     { required: true, message: '', trigger: 'change' }
                 ],
                birthDate: [
                     { required: true, message: '', trigger: 'change' }
                 ],
                eduDegree: [
                     { required: true, message: '', trigger: 'change' }
                 ],
                email:[
                     { required: true, message: '', trigger: 'blur' },
                     { type: 'email', message: '', trigger: ['blur', 'change'] }
                 ],
                UserTags: [
                     { type:'array' ,required: true, message: '', trigger: 'change' }
                 ],
            },
            LoginFormVisible: false,
            RegisterDialogVisible:false,
            formLabelWidth:'55px',
            formLabelWidth1:'100px',
            LoninButtonVisible:true,
            UserDropdownVisible:false,
            UsernameInDropdown:'',
            inputVisible: false,
            inputValue:''
        };
    },
    mounted(){
        this.Index()
    },
    methods: {
        handleSelect(key, keyPath) {
            console.log(key, keyPath);
        },
        Index() {
            this.$ajax.post('/Index/').then(function (response) {//
                // console.log(response);
                if (response['data'] != "") {//null""
                    this.LoninButtonVisible = false;
                    this.UserDropdownVisible = true;
                    this.UsernameInDropdown = response['data'];
                    return true;
                }
            }.bind(this))
                .catch(function (error) {
                    this.open4(error);
                    return false;
                })

        },
        Login() {
            this.LoginFormVisible = true;//this
        },
        Logout(){
             this.$ajax.post('/Logout/').then(function (response) {//
                // console.log(response);
                if (response['data'] != "") {//null""
                    this.LoninButtonVisible = true;
                    this.UserDropdownVisible = false;
                    this.open2("");
                    this.$cookieStore.delCookie('islogin');
                    window.open('/','_self');
                    //this.$router.push({path:'/'})//active
                    return true;
                }
            }.bind(this))
                .catch(function (error) {
                    this.open4(error);
                    return false;
                })
        },
        onSubmit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    console.log('successfully submit!!');
                    this.$ajax.post('/login/', this.$qs.stringify({//qsget uri
                        username: this.form.username,
                        password: this.form.password
                    }))
                        .then(function (response) {
                            if (response['data'] != "") {
                                this.LoginFormVisible = false;
                                this.LoninButtonVisible = false;
                                this.UserDropdownVisible = true;
                                this.UsernameInDropdown = response['data'];
                                this.open2("")
                                this.$cookieStore.setCookie('islogin',1,1296000)//Djangosessionidhttponly
                            }
                            else {
                                this.open4("")
                            }
                        }.bind(this))
                        .catch(function (error) {
                            this.open4(error)
                        })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        onSubmit2(formName){
             this.$refs[formName].validate((valid) => {
                if (valid) {
                    console.log('successfully submit!!');
                    var register_data={
                        username:this.form1.username,
                        password:this.form1.checkpassword,
                        gender:this.form1.gender,
                        birthDate:this.form1.birthDate,
                        eduDegree:this.form1.eduDegree,
                        email:this.form1.email,
                        UserTags:JSON.stringify(this.form1.UserTags)//vueobserver
                    };
                     this.$ajax.post('/register/',this.$qs.stringify(register_data))
                        .then(function (response) {
                        if(response['data']!= ""){//0==""0
                         if(response['data']==2){
                            this.open4(" ()");
                             this.form1.username=''
                        }
                        else if(response['data']==1){
                            this.open2("( ,  )");
                             this.RegisterDialogVisible=false;
                             this.form.username=this.form1.username;
                             this.form.password=this.form1.password;

                        }
                    }
                }.bind(this)).catch(function (error) {
                            this.open4(error)
                        });
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
     handleCommand(command) {
         if(command=="logout"){
            this.open1();
         }
      },
    open1() {
       this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
               this.Logout()
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
    open2(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      open4(text) {
        this.$message.error(text);
        },
      //  about Tag
      handleClose(tag) {
        this.form1.UserTags.splice(this.form1.UserTags.indexOf(tag), 1);
      },
      showInput() {
        this.inputVisible = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      },
      handleInputConfirm() {
        let inputValue = this.inputValue;
        if (inputValue) {
          this.form1.UserTags.push(inputValue);
        }
        this.inputVisible = false;
        this.inputValue = '';
      },
    //  axios,
      axios( url, data,callback){
            this.$ajax.post(url,this.$qs.stringify(data))
                .then(function (response) {
                    if(response['data']!= ""){//0==""0
                        console.log(response['data']);
                        return response['data']
                    }
                }).catch(function (error) {
                            this.open4(error)
                        });
      },
    }
}

</script>

